<template>
    <div class="ide-canvas">
        <div class="header" style="width:100%;height:5%;background-color: #ececec;">
            <div class="editor-tab" style="text-align:center;">
                <span style="font-family:arial;color:grey;font-size:14px;">{{ currentStudent }}</span>
            </div>
        </div>
        <div class="main" style="width:100%;height:95%;">
            <div class="editor-container" style="width:80%;height:100%;float:left;">
                <div ref="editor" style="width:100%;height:100%;float:left;"></div>
            </div>
            <div class="editor-container-sidebar" style="width:20%;height:100%;float:left;align-items:center;">
                <div class="namelist" 
                    v-for="student in students"
                    v-bind:student="student[0]"
                    v-bind:key="student[1]"
                >
                    <el-button round type="primary" @click="chooseStudent(student[0])">{{ student[0]}}</el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            uid: "",
            fontSize: 16,
            students: [],
            currentStudent:"",
        }
    },

    methods: {
        //setValue to the chooseStudent's data
        setValue(){
            const _this = this
            console.log("setValue...")
            for (var key in this.students)
            { 
                console.log(this.students[key][0])
                console.log(this.currentStudent)
                if(this.students[key][0] == this.currentStudent)
                { 
                    console.log("yes!")
                    var data = this.students[key][1]
                    var x = this.students[key][2], y = this.students[key][3]
                    this.editor.setValue(data)
                    this.editor.setPosition({column: x, lineNumber: y})
                    console.log(this.editor.getPosition())
                }
            }
        },
        chooseStudent(value) {
            console.log("buttonValue:" + value)
            this.currentStudent = value
            this.setValue()
        },
        deleteButton(studentId){
            for(var key in this.students)
            {   
                console.log("deleting...")
                if(this.students[key][0] == studentId)
                { 
                    this.students.splice(key, 1)
                    this.editor.setValue("当前会话已关闭")
                    console.log("delete success!")
                }
            }
        }
    },

    mounted() {
        this.editor = monaco.editor.create(this.$refs.editor, {
            value: "//此处显示学生的代码",
            readOnly: true,
            language: "cpp",
            theme: 'vs-light',
            fontSize: this.fontSize,
            folding: true,
        })
    },

    created() {
        const _this = this
        new Promise((resolve, reject) =>{
            this.$axios.post("/api/codemonitor/", {usertype: "teacher"}).then(
                (response) =>{
                    console.log(response.data)
                    this.uid = response.data
                    resolve("solved")
                });
        }).then((response) =>{
                let websocket = new WebSocket('ws://ide-ws.ac-code.com/ws/code/' + this.uid + '/')
                // let websocket = new WebSocket('ws://47.98.127.23:8000/ws/code/' + this.uid + '/')
                // let websocket = new WebSocket('ws://localhost:8000/ws/code/' + this.uid + '/')
                this.websocket = websocket
                this.websocket.onmessage = function(event){
                    var data = JSON.parse(event.data);
                    console.log(data)
                    if (data.message === 'Close!')
                    { 
                        console.log("Closing!")
                        console.log("uid:" + data.uid)
                        _this.deleteButton(data.uid)
                        return;
                    }
                    data = JSON.parse(data.message)
                    console.log(data)
                    var studentId = data.uid
                    var found = false;
                    if (_this.students.length == 0)
                        _this.currentStudent = studentId;
                    for (var key in _this.students)
                    { 
                        if(_this.students[key][0] == studentId){
                            _this.students[key][1] = data.codeValue;
                            _this.students[key][2] = data.posColumn;
                            _this.students[key][3] = data.posRow;
                            console.log(_this.students)
                            found = true;
                            _this.setValue()
                            break;
                        }
                    }
                    if (found == false)
                    {
                        _this.students.push([
                            studentId,
                            data.codeValue,
                            data.posColumn,
                            data.posRow,
                        ])
                        _this.setValue()
                    }
                    // for (var key in _this.students)
                    //     console.log("student: " + _this.students[key])
                    // console.log(_this.students)
                    // for (var key in _this.students)
                    //     console.log(key + "->" + _this.students[key])
                }
                this.websocket.onclose = function(event){
                    console.log(event.code, event.reason, event.wasClean)
                }
            }
        )
    },
    destroyed() {
        this.websocket.close()
    }
}
</script>

<style scoped>
body {
  width: 100%;
  height: 100%;
  font-size: 18px;
  line-height: normal;
  margin: 0;
  padding: 0;
  /* background-color:#0e0d13; */
}
.ide-canvas {
        position: absolute;
        margin: 0;
        margin-top: 0px;
        margin-right: 0px;
        margin-bottom: 0px;
        margin-left: 0px;
        width: 100%;
        height: 100%;
        /* display: flex; */
        /* flex-flow: column; */
        /* flex-direction: column; */
        /* background-color: black; */
    }
.editor-container-sidebar {
    width:20%;
    height:100%;
    float:left;
    display: flex;
    flex-flow: column;
    flex-direction: column;
    align-items:center;
}
.editor-tab {
    width: 15%;
    height: 100%;
    line-height: 30px;
    font-size: 14px;
    color: rgba(51,51,51,.7);
    background-color: white;
    border-right: 1px solid #f3f3f3;
}
</style>


